<template>
  <div class="login-container">
    <div class="head-image"></div>
    <div class="input-view">
      <login-input cla @changeValue="phoneChange" place-holder="请输入手机号"></login-input>
    </div>
    <div class="input-view">
      <login-input @changeValue="passwordChange" place-holder="请输入登陆密码"></login-input>
    </div>
    <div class="register-view">
      <span class="btn-text" @click="forgetPassword">忘记密码</span>
      <span class="btn-text" @click="newUserRegister">新用户注册</span>
    </div>
    <div class="login-btn" @click="loginHandle">登陆</div>
    <div class="foot-view btn-text" @click="visitorHandle">暂不登陆，先看看</div>
  </div>

</template>

<script>
  import loginInput from '../../components/common/loginInput';
  import { navigateTo } from '../../utils/mRouter';

  export default {
    name: 'login',
    components: { loginInput },
    methods: {
      phoneChange(value) {
        console.log(value);
      },
      passwordChange(value) {
        console.log(value);
      },
      loginHandle() {
        navigateTo('../index/index', 'index', '', this);
      },
      visitorHandle() {
        navigateTo('../index/index', 'index', '', this);
      },
      forgetPassword() {
        // navigateTo('../index/index', 'index', '', this);
        alert('莫哟');
      },
      newUserRegister() {
        navigateTo('../register/register', 'register', '', this);
      },
    },
    mounted() {
      console.log(this.$route);
    },
  };
</script>

<style lang="less" scoped>
  @import "../../style/common";
  .login-container {
    .flx-y;
  }
  .head-image {
    .wh(7.5,4.2);
    background-color: gold;
  }
  .btn-text {
    .text-color3;
    .fontSize-five;
  }
  .register-view {
    .flx-x-between;
    .wh(6.06,0.88);
    span {
      .height(0.88);
      .line-height(0.88);
    }
  }
  .login-btn {
    .flx-x-center;
    .wh(6.06,0.88);
    .borderRadius(0.08);
    .fontSize-three;
    background-color: #45D4FB;
    color: white;
  }
  .foot-view {
    position: absolute;
    .height(0.88);
    .line-height(0.88);
    bottom: 20px;
  }
  .input-view {
    .wh(7.5,1.4);
    .flx-y-end;
  }

</style>



